/* Import Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@400;500;700&display=swap');

/* Base styles */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 80px;
  background-color: rgba(10, 10, 10, 0.75);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px); /* Safari support */
  z-index: 1000;
  transition: all 0.3s ease;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

/* Scrolled state */
.scrolled {
  background-color: rgba(5, 5, 5, 0.9);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

/* Container */
.container {
  max-width: 1200px;
  height: 100%;
  margin: 0 auto;
  padding: 0 clamp(1rem, 5vw, 2rem);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* Logo */
.logo {
  font-family: 'Orbitron', sans-serif;
  font-size: clamp(1.5rem, 3vw, 2rem);
  font-weight: 700;
  color: #ffffff;
  text-decoration: none;
  letter-spacing: 0.05em;
  position: relative;
  z-index: 1001; /* Above mobile menu */
}

/* Desktop Navigation */
.desktopNav {
  display: flex;
  align-items: center;
}

.navLinks {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.navLinks li {
  margin-left: clamp(1.5rem, 3vw, 2.5rem);
}

.navLink {
  font-family: 'Orbitron', sans-serif;
  font-size: clamp(0.9rem, 1vw, 1rem);
  color: rgba(255, 255, 255, 0.6);
  text-decoration: none;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  padding: 0.5rem 0;
  position: relative;
  transition: all 0.3s ease;
  cursor: pointer;
}

.navLink:hover {
  color: rgba(255, 255, 255, 1);
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.3);
}

/* Link indicator (active state) */
.linkIndicator {
  position: absolute;
  bottom: -5px;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 2px;
  background: linear-gradient(to right, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0));
  transition: width 0.3s ease;
  opacity: 0;
}

.navLink:hover .linkIndicator {
  width: 100%;
  opacity: 0.5;
}

.navLink.active {
  color: #ffffff;
  font-weight: 500;
}

.navLink.active .linkIndicator {
  width: 100%;
  opacity: 1;
}

/* Mobile Menu Button */
.mobileMenuBtn {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 20px;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  z-index: 1001; /* Above mobile menu */
}

.mobileMenuBtn span {
  width: 100%;
  height: 2px;
  background-color: #ffffff;
  transition: all 0.3s ease;
}

.mobileMenuBtn.open span:nth-child(1) {
  transform: translateY(9px) rotate(45deg);
}

.mobileMenuBtn.open span:nth-child(2) {
  opacity: 0;
}

.mobileMenuBtn.open span:nth-child(3) {
  transform: translateY(-9px) rotate(-45deg);
}

/* Mobile Menu */
.mobileMenu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(5, 5, 5, 0.95);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.mobileNavLinks {
  list-style: none;
  padding: 0;
  margin: 0;
  text-align: center;
}

.mobileNavLinks li {
  margin: 1.5rem 0;
}

.mobileNavLink {
  font-family: 'Orbitron', sans-serif;
  font-size: clamp(1.5rem, 5vw, 2.5rem);
  color: rgba(255, 255, 255, 0.7);
  text-decoration: none;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
}

.mobileNavLink::after {
  content: '';
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 0;
  height: 2px;
  background: linear-gradient(to right, rgba(255, 255, 255, 0), rgba(255, 255, 255, 1), rgba(255, 255, 255, 0));
  transition: width 0.3s ease;
}

.mobileNavLink:hover {
  color: #ffffff;
  text-shadow: 0 0 20px rgba(255, 255, 255, 0.4);
}

.mobileNavLink:hover::after {
  width: 100%;
}

.mobileNavLink.active {
  color: #ffffff;
  font-weight: 500;
}

.mobileNavLink.active::after {
  width: 100%;
}

/* Responsive styles */
@media screen and (max-width: 768px) {
  .desktopNav {
    display: none;
  }
  
  .mobileMenuBtn {
    display: flex;
  }
  
  .navbar {
    height: 70px;
  }
}

@supports not (backdrop-filter: blur(8px)) {
  .navbar {
    background-color: rgba(5, 5, 5, 0.95);
  }
  
  .mobileMenu {
    background-color: rgba(5, 5, 5, 0.98);
  }
}